import $ from 'jquery';
import SCORM from '@szenadam/scorm-api-wrapper';

class AdaptScorm {
  version = '1.8.0';

  $ = $;

  #scorm = null;

  #modules = ['modulo-0.html', 'modulo-1.html', 'modulo-2.html', 'modulo-3.html'];

  #data = {
    curr: 0,
    mod: [
      { id: 0, loc: 0, full: false, pct: 0 },
      { id: 1, loc: 0, full: false, pct: 0 },
      { id: 2, loc: 0, full: false, pct: 0 },
      { id: 3, loc: 0, full: false, pct: 0 },
    ],
  };

  onProgress = () => {};

  constructor() {
    if (!window.top.SAPI) {
      window.top.SAPI = new SCORM(true);
    }

    this.#scorm = window.top.SAPI;

    if (this.#scorm.isAvailable()) {
      this.#scorm.version = '1.2';
      this.#scorm.initialize();

      window.top.onunload = this.#end;
      window.top.onbeforeunload = this.#end;
      window.top.onpagehide = this.#end;

      this.#initialize();
    }
  }

  /* eslint-disable */

  get studentName() {
    const fullname = this.#scorm.dataGet('cmi.core.student_name');
    let arr = fullname.split(',');
    let name = '';

    if (arr[1]) {
      name = arr[1].trim();

      arr = name.split(/\s/g);

      name = arr[0];
    }

    if (!name) {
      name = fullname.replace(',', '').trim();
    }
    return name;
  }

  /* eslint-enable */

  get data() {
    return this.#data;
  }

  get current() {
    return this.#data.curr;
  }

  set current(val) {
    if (val > 0 && !this.#data.mod[val - 1].full) {
      this.gotoModule(val - 1);
      return;
    }

    if (val !== this.#data.curr) {
      this.#data.curr = val;
      this.#sendData();
    }
  }

  setLoc = (index, loc) => {
    this.#data.mod[index].loc = loc;
    this.#sendData();
  };

  gotoModule = (index, loc) => {
    this.#data.curr = index;
    if (loc) this.#data.mod[index].loc = loc;
    this.#sendData();

    window.top.scorm.changeLocation(this.#modules[index]);

    // const { href } = location;
    // if (!href.match(/\.html$/)) {
    //   location.href = `${href}${this.#modules[index]}`;
    // } else {
    //   location.href = href.replace(/[\w-]+\.html$/, `${this.#modules[index]}`);
    // }
  };

  #initialize = () => {
    const dt = this.#scorm.dataGet('cmi.suspend_data');

    if (dt) {
      this.#data = JSON.parse(dt);
    }

    $(() => {
      let htotal = $('html')[0].scrollHeight - window.innerHeight;

      window.addEventListener('resize', () => {
        htotal = $('html')[0].scrollHeight - window.innerHeight;
      });

      setTimeout(() => {
        $('html')[0].scrollTop = this.#data.mod[this.#data.curr].loc;

        window.addEventListener('scroll', (e) => {
          htotal = $('html')[0].scrollHeight - window.innerHeight;

          const pct = Math.round(($('html')[0].scrollTop / htotal) * 100);
          if (pct > this.#data.mod[this.#data.curr].pct) {
            this.#data.mod[this.#data.curr].pct = pct;
            if (pct >= 100) {
              this.#data.mod[this.#data.curr].full = true;
              this.#data.mod[this.#data.curr].pct = 100;

              let score = 0;

              this.#data.mod.forEach((el, index) => {
                if (index > 0) {
                  score += el.full ? 33 : 0;
                }
              });

              if (score === 99) {
                score = 100;

                this.#scorm.dataSet('cmi.core.lesson_status', 'completed');
              }

              this.#scorm.dataSet('cmi.core.score.raw', score);
            }

            this.#sendData();
            this.#sendData();
          }
          this.#data.mod[this.#data.curr].loc = $('html')[0].scrollTop;

          this.onProgress(this.#data.mod[this.#data.curr].pct);
        });
      }, 200);
    });
  };

  #end = () => {
    if (this.#scorm.connectionIsActive) {
      this.#sendData();
      this.#scorm.save();
      this.#scorm.terminate();
    }
  };

  #sendData = () => {
    const str = JSON.stringify(this.#data);

    this.#scorm.dataSet('cmi.suspend_data', str);

    this.#scorm.save();
  };

  send() {
    this.#sendData();
  }
}

export { AdaptScorm };
